body {
  background: #f3f1f2;
  font-family: sans-serif;
}

.app {
  margin: 0rem 0rem;
  display: inline-block;
  /* width: 100%; */
  padding-top: 4px;
}

/* Snow Theme */
.app .ql-snow.ql-toolbar {
  display: flex;
  background: #ffffff;
  // border-top-left-radius: 0.5em;
  // border-top-right-radius: 0.5em;
}

.app .ql-editor {
  min-height: 10.5em;
  left: 0px;
  display: block;
  position: relative;
  padding: 1px;
  text-align: left;
  background-color: white;
}

.ql-editor p {
  color: #333333 !important;
}

.ql-editor li {
  color: #333333 !important;
}

