.ql-container.ql-snow {
  border: 1px solid #ccc;
  width: 359px;
}

.app .ql-container {
  // border-bottom-left-radius: 0.5em;
  // border-bottom-right-radius: 0.5em;
  background: #fefcfc;
  top: 42px;
  height: 143px;
  position: relative;
}

.ql-toolbar.ql-snow {
  border: 1px solid #ccc;
  box-sizing: border-box;
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  padding: 8px;
  position: absolute;
  text-align: center;
  left: 20px;
  display: block;
  width: 92.6%;
}
.app .ql-snow.ql-toolbar .ql-stroke {
  color: black !important;
}

.app .ql-snow.ql-toolbar button.ql-active {
  color: black !important;
}

.app .ql-snow.ql-toolbar button:hover {
  color: black !important;
}
