
.my-recent-container {
    padding: 24px 0px;
    overflow: hidden;
   
    .ant-table-content {
      border-radius: 12px;
      box-shadow: 0 4px 2px 0 rgba(0, 0, 0, 0.1);
      background-color: #ffffff;
      overflow: hidden;
      .ant-table-tbody > tr > td {
        border-bottom: 0px solid #f0f0f0;
        -webkit-transition: background 0.3s;
        transition: background 0.3s;
    }
    }
    .ant-table-container {
      border-radius: 12px;
      box-shadow: 0 4px 2px 0 rgba(0, 0, 0, 0.1);
      background-color: #ffffff;
      overflow: hidden;
    }
    .ant-table {
      border-radius: 12px;
      box-shadow: 0 4px 2px 0 rgba(0, 0, 0, 0.1);
      background-color: #ffffff;
      overflow: hidden;
    }
    .ant-table {
     
      .ant-table-cell:not(:first-child) {
        width: 15%;
      }
      .ant-table-thead {
        tr {
          span {
            // color: #5a5a5a;
            font-size: 14px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
          }
        }
      }
  
      @media screen and (max-width: 600px) {
        &-thead {
          display: none;
        }
  
        &-thead > tr,
        &-tbody > tr {
          th,
          td {
            &:first-of-type {
              padding-top: 1rem;
            }
  
            &:last-of-type {
              padding-bottom: 1rem;
            }
          }
  
          > th,
          > td {
            display: block;
            width: auto !important;
            border: none;
            padding: 0 1rem;
            font-size: 1.1rem;
  
            &:last-child {
              border-bottom: 1px solid #eee;
            }
          }
        }
        .searchMainLyout
        {
      
          background-color: pink;
          position: absolute;
          right:20%;
          top:26.5%;
          width: 150px;
          height: 30px;
        }

      }
    }
  }
  
  @media screen and (max-height: 905px){

